<template>
    <div class="main-form">
        <div class="modal" :style="newModal">
            <h3>Валидация прошла успешно</h3>
        <div @click="translatey=200" class="close-modal" >X</div>
        </div>
        <form action="#" @submit.prevent="checkForm" id="form">
            <p>Поле выделенное знаком * обязательно для заполнения</p>
            <div class="form-item">     
                <label for="firstName">Ваше имя*</label>
                <input v-model.trim="dateInput.FirstName" type="text" id="firstName">
                <span v-if="!$v.dateInput.FirstName.required && $v.dateInput.FirstName.$dirty">Обязательное поле</span>
            </div>
            <div class="form-item">
                <label for="lastName">Ваша фамилия*</label>
                <input v-model.trim="dateInput.lastName" type="text" id="lastName">
                <span v-if="!$v.dateInput.lastName.required && $v.dateInput.lastName.$dirty">Обязательное поле</span>
            </div>
            <div class="form-item">
                <label for="patronymic">Ваше отчество</label>
                <input v-model.trim="dateInput.patronymic" type="text" id="patronymic">
            </div>
            <div class="form-item">
                <label for="date">Год рождения*</label>
                <input v-model.trim="dateInput.date" type="date" id="date">
                <span v-if="!$v.dateInput.date.required && $v.dateInput.date.$dirty">Обязательное поле</span>
            </div>
            <div class="form-item">
                <label for="number">Номер телефона*</label>
                <input v-model.trim="dateInput.number" type="number" id="number">
                <span v-if="!$v.dateInput.number.required && $v.dateInput.number.$dirty ">Обязательное поле</span>
                <span v-if="!$v.dateInput.number.minLength && $v.dateInput.number.$dirty ">Мало цифор</span>
                <span v-if="!$v.dateInput.number.maxLength && $v.dateInput.number.$dirty ">Много цифор</span>
                <span id="num" v-if="!$v.dateInput.number.numberfunc && $v.dateInput.number.$dirty  &&  $v.dateInput.number.maxLength && $v.dateInput.number.minLength && $v.dateInput.number.required">Номер должен начинатся с 7</span>
            </div>
            <div class="form-item">
                <label for="checFloor">Мужской</label>
                <input value="Male" type="radio" id="checFloor" 
                v-model="dateInput.checFloor">
                <label for="checFloor2" >Женский</label>
                <input value="Woman" type="radio" id="checFloor2" 
                v-model="dateInput.checFloor" >

            </div>
             <div class="form-item">
                <label id='one' for="clients">Группа клентов*</label>
                <select id="clients" v-model="dateInput.client" multiple> 
                     <option v-for="(item,index) in Numclients" :key="index" :value="item.value">{{item.label}}</option>    
                </select>
                <span v-if="!$v.dateInput.client.required && $v.dateInput.number.$dirty ">Обязательное поле</span>
             </div>
             <div class="form-item">
                  <label id='one' for="doctors">Врач</label>
                 <select id="doctors" v-model="dateInput.doctors" > 
                     <option value="Ivanov">Иванов</option>
                     <option value="Zakharov">Захаров</option>
                     <option value="Chernysheva">Чернышева</option>            
                </select>
                 </div>
                  <div class="form-item">
                      <label  for="SMS">Отправлять смс</label>
                      <input type="checkbox" id="SMS" v-model="dateInput.sms">
                  </div>
                  <div class="input-adress">
                      <h2>Место жительства</h2>
                      <div class="input-date">
                        <div class="item">
                       <label  for="index">Индекс</label>
                        <input type="number" id="index" v-model="dateInput.adress.index">
                        
                        </div>
                        <div class="item">
                        <label  for="country">Страна</label>
                      <input type="text" id="country" v-model="dateInput.adress.contry">
                      </div>
                      <div  class="item">
                       <label  for="area">Область</label>
                        <input type="text" id="area" v-model="dateInput.adress.area">
                      </div>
                        <div class="item">
                        <label  for="city">Город*</label>
                      <input type="text" id="city" v-model="dateInput.adress.city">
                        <span v-if="!$v.dateInput.adress.city.required && $v.dateInput.adress.city.$dirty ">Обязательное поле</span>
                      </div>

                       <div  class="item">
                       <label  for="street">Улица</label>
                        <input type="text" id="street" v-model="dateInput.adress.street">
                       </div>
                        <div  class="item">
                        <label  for="home">Дом</label>
                      <input type="text" id="home" v-model="dateInput.adress.home">
                      </div>
                      </div>
                  </div>
                  <div class="input-pass">
                    <h2>Паспортные данные</h2>      
                      <div class="input-date">
                          <div class="type-document">
                            <label for="document">Тип документа</label>
                            <span v-if="!$v.dateInput.document.type.required && $v.dateInput.document.type.$dirty ">Обязательное поле</span>
                            <select name="document" id="document" v-model="dateInput.document.type">
                                <option value="pass">Паспорт</option>
                                <option value="birthСertificate">Свидетельство о рождении</option>
                                <option value="rights"> Вод. удостоверение</option>
                            </select>  
                            <div class="pass-date">
                                <div>
                                <label for="Series">Серия</label>
                                <input type="number" id="Series" v-model="dateInput.document.series">
                                </div>
                                <div>
                                <label for="Number">Номер</label>
                                <input type="number" id="Number" v-model="dateInput.document.number">
                                </div>
                                <div>
                                <label for="Extradition">Кем выдан</label>
                                <input type="text" id="Extradition" v-model="dateInput.document.whom">
                                </div>
                                <div>
                                <span class="date-p" v-if="!$v.dateInput.document.date.required && $v.dateInput.document.date.$dirty ">Обязательное поле</span>
                                <label for="date">Дата выдачи</label>
                                <input type="date" id="date" v-model="dateInput.document.date">
                                </div>
                            </div>
                         </div>
                      </div>
                 </div>
                 <input class="submit" type="submit" value="Отправить">
        </form>
    </div>
</template>

<script>
import { required, minLength,maxLength } from 'vuelidate/lib/validators'
import { validationMixin } from 'vuelidate'
export default{
    mixins:[validationMixin],
    data(){
        return{
            translatey:200,
            modal:true,
             Numclients:[
                    {
                        label:'Vip',
                        value:'VIP'
                    },
                    {
                        label:'Проблемные',
                        value:'Problems'
                    },
                    {
                        label:'ОМС',
                        value:'OMS'
                    },
                    ],
            dateInput:{
                FirstName:'',
                lastName:'',
                patronymic:'',
                date:'',
                number:'',
                checFloor:[],
                client:[],
                doctors:'',
                sms:false,
                adress:{
                    index:'',
                    contry:'',
                    area:'',
                    city:'',
                    street:'',
                    home:'',
                },
                document:{
                    type:'',
                    serios:'',
                    number:'',
                    date:'',
                    whom:'',

                },
            }
        }
    },
    validations:{
        dateInput:{
            FirstName:{required,minLength:minLength(3),},
            lastName:{required,minLength:minLength(4),},
            date:{required},
            number:{required,minLength:minLength(11),maxLength:maxLength(11), 
            numberfunc:function(value)
            { return value[0]=='7'}
            },
            client:{required},
            adress:{city:{required}},
            document:{type:{required},date:{required}}
              },
        },
        methods:{
            checkForm(){
                this.$v.dateInput.$touch()
                if( !this.$v.dateInput.$error){
                    console.log('Валидация прошла успешно');
                    this.translatey = 0
                }
            },
        },
         computed:{
             newModal(){
                 return {
                     transform: `translateY(-${this.translatey}px)`
                 }
        }
         }
    }
</script>








<style scoped>

.close-modal{
    position: absolute;
    right: 7px;
    top: 10px;
    cursor: pointer;
}
.modal{
    transition: transform 0.5s;
    padding: 1.2rem;
    position: fixed;
    color: #000;
    z-index: 1000;
    background-color:rgb(219, 217, 122);
    border-radius: 10px;

}
.type-document span{
    right: auto ;
}
.date-p{
    width: 200px;
    top: -24px;
    right: 20px !important; 
    /* right: 0px  !important; */
}
span{
        width: 220px;
    position: absolute;
    top: -20px;
    right: 10px;
    text-align: center;
    font-size: 1rem;
    color: red;
    font-weight: bold;
}
.submit{
    margin: 1.2rem auto 0 ;
    padding: 0.3rem;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.2s;

}
.submit:hover{
    background-color: rgba(247, 234, 192, 0.933);
}
 .main-form p{
     text-align: center;
     width: 100%;
     color: rgb(101, 68, 233);
 }
.main-form{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    display: flex;
    margin: 0 auto;
    max-width: 1251px;
    padding: 1rem;
    justify-content: center;
}
.form-item{
    position: relative;
    padding: 0.4rem;
    justify-content: space-between;
    margin: 10px -10px;
    display: flex;
    width: 300px;
}
form{
        -webkit-box-shadow: 0px -1px 22px 0px rgba(50, 50, 50, 0.51);
-moz-box-shadow:    0px -1px 22px 0px rgba(50, 50, 50, 0.51);
box-shadow:         0px -1px 22px 0px rgba(50, 50, 50, 0.51);
    color: #000;
    background-color:rgba(167, 250, 156, 0.933);
    border-radius: 40px;
    padding: 2rem;
    width: 700px;
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
}
label{
    flex: 1 1 auto;
    margin: 0 10px;
}
input{
    height: 25px;
    flex: 0 1 170px;
    outline:none;
    border-radius: 4px;
}
select{
        outline:none;
    flex: 1 1 auto;
}
#one{
    flex:  0 1 auto;
}
#doctors{
    height: 30px;
}
.input-adress, .input-pass{
    margin: 0 auto;;
}
.input-date{
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
}
.input-date label{
    display: inline-block;
    width: 120px;
}
.item{
    position: relative;
    margin-top: 1.5rem;
}
.pass-date{
    margin-top: 1.3rem;
    display: flex;
    flex-wrap: wrap;
}
.pass-date div {
    position: relative;
    margin-top: 1.5rem;
}
.type-document{
    position: relative;
}



@media(max-width:661px){
    #form{
        justify-content: center;
    }
    .pass-date{
        justify-content: center;
    }
    .pass-date div{
        width: 100%;
    }
}
@media(max-width:451px){
  label{
      display: block !important; 
      text-align: center;   
      width: auto !important;
  }
  .date-p{
    left: 60px;
    width: 200px;
  }
}

@media(max-width:331px){
    span{
        right: 0;
    }
}
</style>